import { BlogEntryInfo } from "@/types/blog";
import { request } from "./index"

export function getArticleIndex(): Promise<Record<string, BlogEntryInfo[]>> {
    return request.get(`/content/blog/blog_index.json`);
}

export function getArticleMarkdown(markdown_name: string): Promise<string> {
    return request.get(`/content/blog/posts/${markdown_name}`, { disable_json: true });
}