import { NetworkError } from "@/types/fetch-class"

export default class Fetch {
    options: FetchOptions | undefined

    constructor(options?: FetchOptions) {
        const options_defaults: FetchOptions = {
            prefix: "",
            disable_json: false,
        }
        options = { ...options_defaults, ...options }
        this.options = options ? options : {}
    }

    private getParamsString(params?: Record<string, string | number | boolean> | undefined): string {
        if (params && params.length > 0) {
            let paramsString = "?"
            for (const key in params) {
                paramsString += `${key}=${params[key]}&`
            }
            return paramsString
        }
        return ""
    }
    private readonly handleResponse = (response: Response,disable_json:boolean): Promise<any> => {
        if (response.ok) {
            if(disable_json) return response.text()
            return response.json()
        }
        throw new NetworkError(response)
    }

    private run(url: string, options: FetchOptions, data?: unknown): Promise<any> {

        const disable_json = options.disable_json || false

        options = { ...this.options, ...options, headers: { ...this.options?.headers, ...options?.headers }, params: { ...this.options?.params, ...options?.params } }
        const param_string = this.getParamsString(options.params)
        const final_url = `${options.prefix}${url}${param_string}`
        if (data !== undefined) options.body = JSON.stringify(data)

        return fetch(final_url, { ...(options as RequestInit) }).then(response =>this.handleResponse(response,disable_json))
    }

    get<T = unknown>(url: string, options?: FetchRequestOptions): Promise<T> {
        return this.run(url, { ...options, method: "GET" })
    }
    post<T = unknown>(url: string, data?: unknown, options?: FetchRequestOptions): Promise<T> {
        return this.run(url, { ...options, method: "POST" }, data)
    }
    put<T = unknown>(url: string, data?: unknown, options?: FetchRequestOptions): Promise<T> {
        return this.run(url, { ...options, method: "PUT" }, data)
    }
    patch<T = unknown>(url: string, data?: unknown, options?: FetchRequestOptions): Promise<T> {
        return this.run(url, { ...options, method: "PATCH" }, data)
    }
    delete<T = unknown>(url: string, options?: FetchRequestOptions): Promise<T> {
        return this.run(url, { ...options, method: "DELETE" })
    }
}